<template>
    <div class="feedback-box">
        <div v-model="idea" class="feedback">
            <p class="titles">{{categoryName}}协议</p>
            <p class="sn" v-if="link_name!='add'">协议流水号：{{sn}}</p>
        </div>
        <div v-if="link_name!='add'">
            <span class="right-tag status0" v-if="status==0"></span>
            <span class="right-tag status4" v-if="status==1"></span>
            <span class="right-tag status1" v-if="status==1 && orderType==1"></span>
            <span class="right-tag status2" v-if="status==2"></span>
            <span class="right-tag status3" v-if="status==10"></span>
        </div>
        <div class="line"></div>
        <div class="section-order mg-t10">
            <div class="matter">
                <p class="title">点匠人:</p>
                <div class="edit">
                    <p class="text"><a @click="showUserInfo(buyer)" style="color: #00afff;">{{buyName}}</a></p>
                </div>
            </div>
            <div class="matter">
                <p class="title">身份证号:</p>
                <div class="edit">
                    <p class="text">{{buyIdentityNumber}}</p>
                </div>
            </div>
            <div class="matter">
                <p class="title">匠人:</p>
                <div class="edit">
                    <p class="text"><a @click="showUserInfo(seller)" style="color: #00afff;">{{sellName}}</a></p>
                </div>
            </div>

            <div class="matter">
                <p class="title">身份证号:</p>
                <div class="edit">
                    <p class="text">{{sellIdentityNumber}}</p>
                </div>
            </div>

            <div class="matter">
                <p class="title">服务名称:</p>
                <div class="edit">
                    <p class="text"><a @click="showService(jobId)" style="color: #00afff;">{{serviceName}}</a></p>
                </div>
            </div>

            <div class="matter">
                <p class="title">服务期限:</p>
                <div class="edit" v-link="{name:'choose-date',query:{from:from,type:type,targetType:2}}">
                    <p class="text none" v-if="workDay.startTime.length == 0 ">请选择服务日期（必选）</p>
                    <p class="text ellipsis" v-else>
                        <span v-if="workDay.startTime == workDay.endTime">{{workDay.startTime}}</span>
                        <span v-else>{{workDay.startTime}}-{{workDay.endTime}}</span>

                        <span v-if="workDay.startTime.length > 0">({{workDay.split | week-format}})</span>
                    </p>
                    <span class="img transfer"></span>
                </div>
            </div>

            <div class="matter">
                <p class="title">服务时间:</p>
                <div class="edit" v-link="{name:'choose-time',query:{from:from,type:type,targetType:2}}">
                    <p class="text none" v-if="workTime.length == 0 ">请选择服务时间（必选）</p>
                    <p class="text ellipsis" v-else>{{workTime}}</p>
                    <span class="img transfer"></span>
                </div>
            </div>

            <div class="matter">
                <p class="title">服务地址:</p>
                <div class="edit" v-link="{name:'choose-address',query:{from:from,type:type,targetType:2}}">
                    <p class="text none" v-if="address.length == 0 ">请填写详细服务地址</p>
                    <p class="text ellipsis" v-else>{{address}}</p>
                    <span class="img transfer"></span>
                </div>
            </div>

            <div class="address-box">
                <p class="title">服务说明:</p>
                <div class="edit">
                    <textarea placeholder="请填写服务说明(可不填)" v-model="description" class="address" rows=4></textarea>
                </div>
            </div>

            <div class="tables">
                <div class="warp1">
                    <ul style="background-color: #fef7ed;height: 100%;display: flex;border-radius: .05rem;">
                        <li class="li"><span>服务单价</span><span class="monad">(元)</span></li>
                        <li class="li"><span>总工时</span><span class="monad">(时)</span></li>
                        <li class="li"><span>总价</span><span class="monad">(元)</span></li>
                    </ul>
                </div>
                <div class="warp2">
                    <ul style="height: 100%;display: flex;">
                        <li class="li" style="color: #333333;"><input v-model="price" min="0" max="999999" class="price-input publish-lis" type="number"
                                                                      onkeyup="value=value.replace(/[^\d]/g,'1')"/></li>
                        <li class="li" style="color: #333333;"><span>{{hours}}</span></li>
                        <li class="li" style="color: #333333;"><span>{{totalPrice}}</span></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    import {toast} from '../assets/js/toast'
    import {url, st, order, obj} from '../const'
    import {DJUtils} from '../assets/js/common'
    import {imgHost} from '../filter'

    export default{
        props: {
            link_name: {type: String, default: 'add'}
        },
        ready(){
            let vm = this;
            vm.$on('reload-child', function () {
                let link = vm.link_name;
                if (link == 'add') {
                    vm.type = 1;
                    vm.initAddData();
                } else if (link == 'edit') {
                    vm.type = 2;
                    let news = order.get(vm.type);
                    let params = vm.$route.params;
                    //修改的发布和请求的发布一样时 直接拿本地的发布
                    if (news && news.id == params.id && news.getTime + 1800 >= parseInt(new Date().getTime() / 1000)) {
                        vm.setData(news);
                        vm.calMoney();
                        vm.notify();
                    } else {
                        vm.getOrder(params.id);
                    }
                }
            });
            vm.$emit('reload-child');

            $(".price-input").blur(function () {
                let _this = $(this);
                if ((!vm.price || !/^[0-9]*[1-9][0-9]*$/.test(vm.price))) {
                    toast.alert('技能售价必须大于0且不能有小数');
                }
                vm.notify();
            });
            $(".price-input").keyup(function () {
                vm.totalPrice = vm.hours * vm.price;
                vm.notify();
            });

            vm.from = vm.$route.path;

            //注册事件
            vm.$on('publish', vm.doPublish);
            vm.$on('save-publish', vm.savePublish);
            vm.$on('back-tip', function () {
                if (this.type == 1) {
                    let backEnd = this.dataToStr();
                    if (backEnd != vm.backEnd) {
                        toast.conform('数据已修改,确定退出吗？', function () {
                            DJUtils.backHistory();
                        }, true, null, {do: '退出', cancel: '取消'});
                    } else {
                        DJUtils.backHistory();
                    }
                } else {
                    let dataStr = vm.dataToStr();
                    let backEnd = order.get(this.type).backEnd;
                    if (dataStr == backEnd) {
                        DJUtils.backHistory();
                    } else {
                        toast.conform('数据已修改,确定退出吗？', function () {
                            DJUtils.backHistory();
                        }, true, null, {do: '退出', cancel: '取消'});
                    }
                }
            });
        }
        ,
        data()
        {
            return {
                id: 0,
                sn: '',
                jobId: '',
                buyName: '',
                buyIdentityNumber: '',
                sellName: '',
                sellIdentityNumber: '',
                buyer: '',
                seller: '',
                serviceName: '',
                categoryName: '',
                category:'',
                description: '',
                workDay: {startTime: '', endTime: '', split: ''},
                price: '',
                address: '',
                workTime: '',
                status: '',
                hours:0,
                totalPrice:0,
                //跳转
                from: '',
                type: 0,
                targetType:0,//1=发布2=协议
                isEdit: false,
                backEnd: '',

                resultData:{items:[]}
            }
        },
        methods: {
            initAddData(){
                let vm = this;
                let params = vm.$route.params;
                let jobId = params.id;
                vm.$http.get(url.spotArtisan, {jobId: jobId}).then(
                        function ({data}) {
                            if (data.success) {
                                let resultData = data.data;
                                if (resultData) {
                                    this.setData(vm.buildOrder(resultData));
                                    this.backEnd = this.dataToStr();
                                    if (!st.has(st.keys.orderAdd)) {
                                        st.set(st.keys.orderAdd, vm.buildOrder(resultData));
                                    }
                                    this.setData(order.get(vm.type));
                                }
                                vm.calMoney();
                                vm.notify();
                            } else {
                                toast.alert(data.resultMsg);
                            }
                        }
                )
            },
            buildOrder(data){
                let o = {
                    id: data.id,
                    sn: data.sn,
                    jobId: data.jobId,
                    buyer: data.buyer,
                    seller: data.seller,
                    buyName: data.buyName,
                    buyIdentityNumber: data.buyIdentityNumber,
                    sellName: data.sellName,
                    sellIdentityNumber: data.sellIdentityNumber,
                    serviceName: data.serviceName,
                    categoryName: data.categoryName,
                    category:data.category,
                    description: data.description,
                    workDay: {startTime: data.startTime, endTime: data.endTime, split: data.workSplit},
                    price: data.price,
                    status:data.status,
                    address: data.address,
                    workTime: data.workTime,
                    getTime: parseInt(new Date().getTime() / 1000)
                };
                return o;
            },
            setData(order){
                this.id = order.id;
                this.sn = order.sn;
                this.jobId = order.jobId;
                this.buyer = order.buyer;
                this.seller = order.seller;
                this.buyName = order.buyName;
                this.buyIdentityNumber = order.buyIdentityNumber;
                this.sellName = order.sellName;
                this.sellIdentityNumber = order.sellIdentityNumber;
                this.serviceName = order.serviceName;
                this.description = order.description;
                this.workDay = order.workDay;
                this.price = order.price;
                this.address = order.address;
                this.workTime = order.workTime;
                this.status = order.status;
                this.categoryName = order.categoryName;
                this.category = order.category;
            },
            checkEdit(){
                if (!this.price || !/^[0-9]*[1-9][0-9]*$/.test(this.price)) {
                    return false;
                } else if (!this.workDay.startTime) {
                    return false;
                } else if (!this.workTime) {
                    return false;
                }
                return true;
            },
            notify(){
                this.isEdit = this.checkEdit();
                this.$dispatch('edit-status', this.isEdit);
                let news = order.get(this.type);
                news.description = this.description;
                news.price = this.price;
                order.set(news,this.type);
            },
            calMoney(){
                let vm = this;
                //计算时间和金额
                if (DJUtils.isNotBlank(vm.workDay.startTime) && DJUtils.isNotBlank(vm.workDay.endTime) && DJUtils.isNotBlank(vm.workDay.split)
                        && DJUtils.isNotBlank(vm.workTime)){
                    vm.hours = DJUtils.calValidDays(vm.workDay.startTime,vm.workDay.endTime,vm.workDay.split,vm.workTime);
                    vm.totalPrice = vm.hours * vm.price;
                }
            },
            savePublish(){
                let vm = this;
                if (!vm.isEdit) {
                    return;
                }

                if (this.link_name == 'add') {
                    vm.$http.get(url.addAgreement, vm.editData(0)).then(
                            function ({data}) {
                                if (data.success) {
                                    toast.alert('保存成功');
                                    order.remove(vm.type);//清空缓存
                                    //返回待发布
                                    window.location.href = "#/order?status=0&orderType=0";
                                    /* window.history.back();*/
                                } else {
                                    toast.alert(data.resultMsg);
                                }
                            }
                    )
                }

                if (this.link_name == 'edit') {
                    let dataStr = vm.dataToStr();
                    let backEnd = order.get(this.type).backEnd;

                    if (dataStr == backEnd) {
                        toast.alert('数据无变化');
                        return;
                    }
                    vm.$http.get(url.updateAgreement, vm.editData(vm.status)).then(
                            function ({data}) {
                                if (data.success) {
                                    toast.alert('保存成功');
                                    order.remove(vm.type);//清空缓存
                                    window.history.back();
                                } else {
                                    toast.alert(data.resultMsg);
                                }
                            }
                    )
                }
            },
            doPublish(){
                let vm = this;
                if (this.link_name == 'add') {
                    //新建界面的发布是保存并发布，转态传1
                    vm.$http.get(url.addAgreement, vm.editData(1)).then(
                            function ({data}) {
                                if (data.success) {
                                    toast.alert('发布成功');
                                    order.remove(vm.type);//清空缓存

                                    //返回发布中
                                    window.location.href = "#/order?status=1&orderType=0";
                                } else {
                                    if (data.code == 201){
                                        vm.resultData.items = data.data;
                                        this.$dispatch('result-data', vm.resultData);
                                        this.$dispatch('job-id', vm.jobId);
                                        console.log(vm.resultData);
                                        setTimeout(function () {
                                            $('.bookedpop').css('display','block');
                                            toast.mask(function () {
                                                toast.hide_mask();
                                                $('.bookedpop').css('display', 'none');
                                            }, true);
                                        }, 100);
                                    }else{
                                        toast.alert(data.resultMsg);
                                    }
                                }
                            }
                    )
                }
            },
            getOrder(id){
                toast.loading();
                this.$http.get(url.agreementDetailShow, {agId: id}).then(
                        function ({data:{success, data, resultMsg, code}}) {
                            if (success) {
                                let o = this.buildOrder(data);
                                this.setData(o);
                                this.$dispatch('order-status', this.status);//发送状态
                                o.backEnd = this.dataToStr();
                                order.set(o, this.type);
                                this.calMoney();
                                this.notify();
                            } else {
                                toast.alert(resultMsg);
                            }
                        }, function (response) {
                            toast.alert('网络异常');
                        }
                )
            },
            dataToStr(){
                let vm = this;
                let data = {
                    id: vm.id,
                    price: vm.price,
                    startTime: vm.workDay.startTime,
                    endTime: vm.workDay.endTime,
                    workTime: vm.workTime,
                    validWeek: vm.workDay.split,
                    description: vm.description,
                    address: vm.address
                };
                let backEnd = '';
                for (let i in data) {
                    backEnd += (data[i]==null ? '' : data[i] + '');
                }
                return backEnd;
            },
            editData(status){
                let vm = this;
                let postData = {
                    id: vm.id,
                    jobId:vm.jobId,
                    category:vm.category,
                    seller: vm.seller,
                    serviceName: vm.serviceName,
                    price: vm.price,
                    startTime: vm.workDay.startTime,
                    endTime: vm.workDay.endTime,
                    workTime: vm.workTime,
                    validWeek: vm.workDay.split,
                    description: vm.description,
                    address: vm.address,
                    status: status
                };
                return postData;
            },
            showUserInfo (userId){
                let vm = this;
                console.log('userId=' + userId);
                vm.$http.get(url.agUserShow, {
                    userId: userId
                }).then(
                        function ({data}) {
                            if (data.success) {
                                vm.userInfo = data.data;
                                vm.userInfo.icon = imgHost(vm.userInfo.icon);
                                this.$dispatch('user-info', vm.userInfo);
                                setTimeout(function () {
                                    $('.userInfo').css('display', 'block');
                                    $(".userInfo").addClass('tran-scale1');
                                    $(".userInfo").fadeIn(100);
                                    toast.mask(function () {
                                        toast.hide_mask();
                                        $('.userInfo').css('display', 'none');
                                    }, true);
                                    $(".userInfo").removeClass('tran-scale1');
                                }, 100);
                            } else {
                                toast.alert(data.resultMsg);
                            }
                        }
                );
            },
            showService (serviceId){
                console.log('serviceId=' + serviceId);
                window.location.href = `#/service/${serviceId}`;
            }
        },
        watch:{
           'description':function(){
              this.notify();
           }
        }
    }
</script>
<style scoped>
    .section-order .matter {
        height: .45rem;
        line-height: .45rem;
        font-size: .14rem;
        border-bottom: .01rem solid #efefef;
        margin-left: .1rem;
        position: relative;
    }

    .matter .title {
        margin-left: .1rem;
        float: left;
        height: 100%;
        width: .7rem;
    }

    .matter.more {
        height: auto;
    }

    .edit .unit {
        position: absolute;
        right: .1rem;
        top: 0;
        color: #ffb600;
    }

    .matter .edit-area {
        height: .8rem;
        line-height: 1.4;
        width: 100%;
        margin-top: .1rem;
        padding-right: .1rem;
    }

    .edit.more {
        height: .8rem;
        width: 100%;
        margin-top: .1rem;
        padding: 0 .1rem;
        line-height: 1.4;
        clear:both;
    }

    .edit .text {
        color: #333;
    }

    .edit .text.none {
        color: #999;
    }

    .edit input[type='text'] {
        width: 100%;
        text-indent: 0;
        padding: 0;
    }

    .feedback-box {
        border: .01rem solid #e4e4e4;
        box-shadow: 2px 2px 5px #d8d8d8;
        background-color: #fff;
        margin: .1rem;
        border-radius: .05rem;
        position: relative;
    }
    .feedback-box:before {
        box-shadow: -2px -2px 5px #D8D8D8;
        content: "";
        border-radius: .05rem;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: -1;
    }

    .feedback {
        margin: .25rem auto .15rem auto;
        width: 100%;
    }

    .titles {
        color: #333333;
        font-size: 25px;
        text-align: center;
    }

    .sn {
        color: #9a9a9a;
        font-size: 13px;
        text-align: right;
        margin: .1rem .1rem auto auto;
    }

    .right-tag {
        background-image: url(../assets/img/icon/tag.png);
        width: 75px;
        height: 65px;
        position: absolute;
        top: -.05rem;
        right: -.05rem;
        background-size: 375px 65px;
    }

    .status0 {
        background-position: -75px 0;
    }

    .status1 {
        background-position: 0 0;
    }

    .status2 {
        background-position: -150px 0;
    }

    .status3 {
        background-position: -225px 0;
    }

    .status4 {
        background-position: -300px 0;
    }

    .tables {
        height: 1rem;
        margin: .2rem .1rem .2rem;
        border: 1px solid #eba752;
        border-radius: .05rem;
    }

    .warp1 {
        height: 45%;
    }

    .li {
        text-align: center;
        -webkit-box-flex: 1;
        flex: 1;
        margin-top: .15rem;
    }

    .li .monad {
        color: #bbbbbb;
    }

    .warp2 {
        height: 55%;
    }

    .section-order {
        border-top: .01rem solid #efefef;
        background-color: #fff;
    }

    .price-input {
        border: 1px solid #e8e8e8;
        width: 80%;
        height: 30px;
        float: left;
        margin-left: .1rem;
        margin-top: -3px;
        text-align: center;
        border-radius: .03rem;
    }

    .address-box {
        border-bottom: .01rem solid #efefef;
        background-color: #fff;
        font-size: .14rem;
        margin-left: .1rem;
    }

    .address-box .edit{
        width: 100%;
        padding-left: .8rem;
        height: auto;
    }

    .address-box .address {
        width: 100%;
        line-height: 1.4;
        margin-bottom: .1rem;
        padding-right: .1rem;
        margin-top: .1rem;
    }

    .address-box .title {
        margin-left: .1rem;
        float: left;
        width: .7rem;
        text-align: left;
        margin-top: .1rem;
    }
</style>



