<template>
    <div>
        <v-header>
            <div slot="right" :class="['h-right',isEdit ? '' : 'opa-6']"><a @click="save" class="mg-r10">保存</a></div>
        </v-header>

        <v-content :type="'form'">
            <v-order-detail class="publish-detail" :link_name="'add'">

            </v-order-detail>
        </v-content>
        <v-user-info-pop :user_info="userInfo"></v-user-info-pop>
        <v-bookedpop class="bookedpop z-index-max tran-scale" :result_data="resultData" :job_id="jobId"></v-bookedpop>
        <v-footer-btn>
            <input type="button" value="直接发起" :class="['input','max',isEdit ? 'ok' : 'nk']" @click="publish"/>
        </v-footer-btn>
    </div>
</template>
<style>
    .bookedpop {
        position: absolute;
        border-radius: .05rem;
        top: 25%;
        left: 12%;
        text-align: center;
        width: 76%;
        background-color: #fff;
        display: none;
    }
</style>
<script>
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'
    import VFooterBtn from '../../components/FooterBtn'
    import VOrderDetail from '../../components/OrderDetail'
    import VBookedpop from '../../components/Bookedpop'
    import VUserInfoPop from '../../components/UserInfoPop'
    import $ from 'jquery'

    export default{
        route: {
            data({from,to}){
                if (this.isReady) {
                    return {
                        state: !this.state
                    }
                }
            }
        },
        ready(){
            this.isReady = true;
        },
        data(){
            return {
                isReady: false,
                state: false,
                isEdit: false,

                resultData:{items:[]},
                jobId:0,
                userInfo:{
                    icon:'',
                    nickName:'',
                    sex:0,
                    age:0,
                    realName:'',
                    phone:'',
                    qq:'',
                    wx:'',
                    email:''
                }
            }
        },
        methods: {
            save(){
                if (this.isEdit) {
                    this.$broadcast('save-publish');
                }
                ;
            },
            publish(){
                if (this.isEdit) {
                    this.$broadcast('publish');
                }
                ;
            }
        },
        events: {
            'edit-status': function (isEdit) {
                this.isEdit = isEdit;
            },
            'result-data': function (resultData) {
                this.resultData = resultData;
                console.log('result-data:'+this.resultData)
            },
            'job-id': function (jobId) {
                this.jobId = jobId;
                console.log('job-id:'+jobId)
            },
            'user-info': function (userInfo) {
                this.userInfo = userInfo;
            },
            'backtip': function () {
                this.$broadcast('back-tip');
            }
        },
        watch: {
            'state': function () {
                this.isEdit = false;
                this.$broadcast('reload-child');
                $('.bookedpop').css('display', 'none');
                $('.userInfo').css('display', 'none');
            }
        },
        components: {VHeader, VContent, VFooterBtn, VOrderDetail,VBookedpop,VUserInfoPop}
    }
</script>





