<template>
    <div class="bookedpop z-index-max tran-scale">
        <div class="booked-block">
            <p class="title">已被预约</p>
            <p class="description">以下服务期限已被其他人预约，请参考匠人日程进行修改。</p>
            <div class="date-list">
                <ul v-if="result_data.items.length >0">
                    <li><span class="item-left">{{result_data.items[0]}}</span><span class="item-right" v-if="result_data.items[1]">{{result_data.items[1]}}</span><span v-else class="item-right"></span></li>
                    <li v-if="result_data.items.length >2"><span class="item-left">{{result_data.items[2]}}</span><span class="item-right" v-if="result_data.items[3]">{{result_data.items[3]}}</span><span v-else class="item-right"></span></li>
                    <li v-if="result_data.items.length >4"><span class="item-left">{{result_data.items[4]}}</span><span class="item-right" v-if="result_data.items[5]">{{result_data.items[5]}}</span><span v-else class="item-right"></span></li>
                    <li v-if="result_data.items.length >6"><span class="item-left">{{result_data.items[6]}}</span><span class="item-right" v-if="result_data.items[7]">{{result_data.items[7]}}</span><span v-else class="item-right"></span></li>
                    <li v-if="result_data.items.length >8"><span class="item-left">{{result_data.items[8]}}</span><span class="item-right" v-if="result_data.items[9]">{{result_data.items[9]}}</span><span v-else class="item-right"></span></li>
                </ul>
            </div>
            <p v-if="result_data.items.length > 10">...</p>
        </div>
        <p class="link-calendar" v-link="{name:'his-calendar',params:{id:job_id}}"><a style="color: #03c5a3;font-size: .14rem;">查看匠人日程</a></p>
    </div>
</template>
<style  scoped>
    .bookedpop {
        position: absolute;
        border-radius: .05rem;
        top: 50%;
        left: 50%;
        text-align: center;
        width: 80%;
        background-color: #fff;
        display: none;
        margin: -50% 0 0 -40%;
    }
    .booked-block {
        /* border-bottom: 1px solid; */
        height: 85%;
    }

    .booked-block .title {
        font-size: .16rem;
        color: #333;
        margin-top: .15rem;
    }

    .booked-block .description {
        text-align: left;
        color: #666;
        font-size: .14rem;
        margin: .15rem .15rem 0 .15rem;

    }

    .booked-block .date-list {
        text-align: center;
        margin-top: .15rem;
        height: 1.5rem;
    }

    .booked-block .item-left {
        margin-right: .5rem;
        font-size: .14rem;
        color: #666;
    }

    .booked-block .item-right {
        font-size: .14rem;
        color: #666;
        display: inline-block;
        width: .35rem;
    }
    .booked-block li {
        padding-top: .05rem;
    }
    .link-calendar {
        border-top: 1px solid #efefef;
        line-height: .4rem;
    }
</style>
<script>
    export default{
        props: {
            result_data: {type: Object, default: {}},
            job_id:{type:Number,default:0}
        },
        ready(){
            console.log('book:'+this.result_data.items);
        },
        data(){
            return {
                msg: 'hello vue'
            }
        },
        components: {}
    }
</script>



