<template>
    <div class="userInfo z-index-max tran-scale">
        <div class="part1">
            <p class="avatar" :style="user_info.icon ? 'background-image:url('+ user_info.icon +')' : ''" v-if="user_info.icon != null"></p>
            <p :class="['avatar','icon',user_info.sex == 1 ? 'sir' : 'lady']" v-else></p>
            <p class="part2">
                <span class="nick-name">{{user_info.nickName}}</span>
                <span class="sir" v-if="user_info.sex==1"></span>
                <span class="lady" v-else></span>
                <span class="age">{{user_info.age}}</span>
            </p>
        </div>
        <div class="part-line"></div>
        <div class="part2">
            <ul>
                <li><span class="info-icon name"></span><span class="info-text">{{user_info.realName}}</span></li>
                <li><span class="info-icon phone"></span><a href="tel:{{user_info.phone}}"><span class="info-text">{{user_info.phone}}</span></a></li>
                <li v-if="user_info.qq"><span class="info-icon qq"></span><span class="info-text">{{user_info.qq}}</span></li>
                <li v-if="user_info.wx"><span class="info-icon wx"></span><span class="info-text">{{user_info.wx}}</span></li>
                <li><span class="info-icon email"></span><span class="info-text">{{user_info.email}}</span></li>
            </ul>
        </div>
    </div>
</template>
<style scoped>
    @media screen and (max-width: 320px){
        .userInfo {
            width: 80%;
            background-color: #fff;
            border: 1px solid #e4e4e4;
            border-radius: .05rem;
            position: absolute;
            display: none;
            left: 50%;
            top: 50%;
            margin: -50% 0 0 -40%;
        }
    }

    @media screen and (min-width: 321px){
        .userInfo {
            width: 70%;
            background-color: #fff;
            border: 1px solid #e4e4e4;
            border-radius: .05rem;
            position: absolute;
            display: none;
            left: 50%;
            top: 50%;
            margin: -50% 0 0 -35%;
        }
    }


    .avatar {
        width: .65rem;
        height: .65rem;
        background-size: .65rem .65rem;
        margin: .2rem auto.05rem;
        border-radius: 50%;
    }

    .userInfo .icon.lady {
        background-image: url(../assets/img/user/user_n_lady.png);
    }

    .userInfo .icon.sir {
        background-image: url(../assets/img/user/user_n_sir.png);
    }

    .nick-name {
        font-size: .16rem;
        margin-left: .28rem;
    }

    .age {
        font-size: 14px;
        margin-left: 15px;
        color: #bbb;
        vertical-align: middle;
    }

    .info-icon.name {
        background-position: 0 0;
    }

    .info-icon.phone {
        background-position: 0 -24px;
    }

    .info-icon.qq {
        background-position: 0 -48px;
    }

    .info-icon.wx {
        background-position: 0 -72px;
    }

    .info-icon.email {
        background-position: 0 -96px;
    }

    .info-icon {
        width: 14px;
        height: 14px;
        background-image: url("../assets/img/icon/i_msg.png");
        background-size: 14px 110px;
        display: inline-block;
    }

    .userInfo .lady {
        background-image: url(../assets/img/icon/lady.png);
        width: 12px;
        height: 12px;
        position: absolute;
        background-size: 12px 12px;
        margin-top: 7px
    }

    .userInfo .sir {
        background-image: url(../assets/img/icon/sir.png);
        width: 12px;
        height: 12px;
        position: absolute;
        background-size: 12px 12px;
        margin-top: 7px
    }

    .part1 {
        text-align: center;
    }

    .part2 {
        position: relative;
    }

    .part2 ul {
        padding: 30px;
    }

    .part2 ul li {
        /*position: relative;*/
        /*left: 30px;*/
        /*top: 40px;*/
        line-height: 25px;
    }

    .info-text {
        font-size: .16rem;
        margin-left: .1rem;
    }

    .part-line {
        position: absolute;
        top: 1.2rem;
        left: 0;
        right: 0;
        height: .45rem;
        border-top: .01rem solid #e5e5e5
    }
</style>
<script>
    export default{
        props: {
            user_info: {type: Object, default: {}}
        },
        components:{
        }
    }
</script>



